.profile__wrapper{
    .profile__cover{
        height: 326px;

        background-repeat: no-repeat;
        background-size:100% 100%;

        @media (max-width:678px) {
            height: 230px;
        }

        .container{
            gap: 15px;
            justify-content: end;

            @media (max-width:678px) {
                justify-content: center;
            }

            button{
                margin-top: 266px;

                gap: 12px;
                border: 2px solid #777E91;
                border-radius: 90px;

                padding: 12px 16px;

                font-family: 'DM Sans';
                font-style: normal;
                font-weight: 700;
                font-size: 14px;
                line-height: 16px;

                color: #FCFCFD;

                color: #FCFCFD;
                @media (max-width:678px) {
                    margin-top: 134px;
                    font-size: 12px;
                }
            }
        }
    }

    .profile__main{
        display: flex;
        @media (max-width:678px) {
            flex-direction: column;
        }
        .main__user{
            padding: 35px;
        }
    }
}